import { useState, useEffect, useCallback } from "react";
import {
  getAllBrands,
  getAllFeaturedBrands,
  getAllUtsavFeaturedBrands,
  getBrand,
} from "../api/brandApis";

const useBrand = (brandId) => {
  const [brands, setBrands] = useState([]);
  const [featuredBrands, setFeaturedBrands] = useState([]);
  const [utsavFeaturedBrands, setUtsavFeaturedBrands] = useState([]);
  const [brand, setBrand] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const fetchAllBrands = useCallback(async () => {
    if (brandId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = "brands";
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setBrands(JSON.parse(cachedData));
      } else {
        const response = await getAllBrands();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.brandList)
          );
          setBrands(response.data.brandList);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, [brandId]);

  const fetchAllFeaturedBrands = useCallback(async () => {
    if (brandId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = "featured_brands";
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setFeaturedBrands(JSON.parse(cachedData));
      } else {
        const response = await getAllFeaturedBrands();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.brandDetails)
          );
          setFeaturedBrands(response.data.brandDetails);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, [brandId]);

  const fetchAllUtsavFeaturedBrands = useCallback(async () => {
    if (brandId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = "utsav_featured_brands";
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setUtsavFeaturedBrands(JSON.parse(cachedData));
      } else {
        const response = await getAllUtsavFeaturedBrands();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.productDetails)
          );
          setUtsavFeaturedBrands(response.data.productDetails);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, [brandId]);

  const fetchBrand = useCallback(async () => {
    if (!brandId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = `brand_${brandId}`;
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setBrand(JSON.parse(cachedData));
      } else {
        const response = await getBrand(brandId);

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.brandDetails)
          );
          setBrand(response.data.brandDetails);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, [brandId]);

  useEffect(() => {
    fetchAllBrands();
  }, [fetchAllBrands]);

  useEffect(() => {
    fetchAllFeaturedBrands();
  }, [fetchAllFeaturedBrands]);

  useEffect(() => {
    fetchAllUtsavFeaturedBrands();
  }, [fetchAllUtsavFeaturedBrands]);

  useEffect(() => {
    fetchBrand();
  }, [fetchBrand]);

  return { brands, featuredBrands, utsavFeaturedBrands, brand, loading, error };
};

export default useBrand;
