import { useState, useEffect, useCallback } from "react";
import {
  fetchAllUtsavVariants,
  fetchProduct,
  getAllFeaturedVariants,
  getAllNewArrivals,
} from "../api/productApis";

const useProduct = (productId) => {
  const [product, setProduct] = useState(null);
  const [utsavVariants, setUtsavVariants] = useState([]);
  const [featuredVariants, setFeaturedVariants] = useState([]);
  const [newArrivals, setNewArrivals] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const getProduct = useCallback(async () => {
    if (!productId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = `product_${productId}`;
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setProduct(JSON.parse(cachedData));
      } else {
        const response = await fetchProduct(productId);

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(cacheKey, JSON.stringify(response.data));
          setProduct(response.data);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error);
    } finally {
      setLoading(false);
    }
  }, [productId]);

  const getAllUtsavVariants = useCallback(async () => {
    setLoading(true);
    setError(null);
    try {
      const cacheKey = "all_utsav_variants";
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setUtsavVariants(JSON.parse(cachedData));
      } else {
        const response = await fetchAllUtsavVariants();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.productDetails)
          );
          setUtsavVariants(response.data.productDetails);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error);
    } finally {
      setLoading(false);
    }
  }, []);

  const fetchAllFeaturedVariants = useCallback(async () => {
    setLoading(true);
    setError(null);
    try {
      const cacheKey = "all_featured_variants";
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setFeaturedVariants(JSON.parse(cachedData));
      } else {
        const response = await getAllFeaturedVariants();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.variantDetails)
          );
          setFeaturedVariants(response.data.variantDetails);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error);
    } finally {
      setLoading(false);
    }
  }, []);

  const fetchAllNewArrivals = useCallback(async () => {
    setLoading(true);
    setError(null);
    try {
      const cacheKey = "all_new_arrivals";
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setNewArrivals(JSON.parse(cachedData));
      } else {
        const response = await getAllNewArrivals();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.productDetails)
          );
          setNewArrivals(response.data.productDetails);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error);
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    getProduct();
  }, [getProduct]);

  useEffect(() => {
    getAllUtsavVariants();
  }, [getAllUtsavVariants]);

  useEffect(() => {
    fetchAllFeaturedVariants();
  }, [fetchAllFeaturedVariants]);

  useEffect(() => {
    fetchAllNewArrivals();
  }, [fetchAllNewArrivals]);

  return {
    product,
    utsavVariants,
    featuredVariants,
    newArrivals,
    loading,
    error,
  };
};

export default useProduct;
