import { FaHeart, FaRegHeart } from "react-icons/fa";
import { Link, useNavigate } from "react-router-dom";
import { useWishlist } from "../contexts/wishlistContext";
import RatingBadge from "./RatingBadge";
import useReview from "../hooks/useReview";
import { useState } from "react";
import Carousel from "./Carousel";
import utsavBadge from "../assets/images/utsav-badge.png";

const ProductCard = ({ product, dropdown = false }) => {
  const navigate = useNavigate();
  const { addToWishlist, removeFromWishlist, isWished } = useWishlist();
  const { averageRating, totalReviews, ratingsBreakdown } = useReview(
    product.productGroup._id
  );
  const [isHovered, setIsHovered] = useState(false);

  const toggleWishlist = (e) => {
    e.stopPropagation();

    if (isWished(product._id)) {
      removeFromWishlist(product._id);
    } else {
      addToWishlist(product._id);
    }
  };

  return (
    <div
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      className="w-50 max-md:w-40 h-[300px] max-md:h-[248px] flex flex-col border border-secondary rounded-xl overflow-hidden hover:shadow-md"
    >
      <div
        onClick={() => navigate(`/product/${product._id}`)}
        className="ITEM-IMAGES relative w-full h-50 max-md:h-40 cursor-pointer"
      >
        {isHovered &&
        product?.productGroup?.otherImages &&
        product?.productGroup?.thumbnail ? (
          <Carousel pagination autoplay delay={2000} loop>
            {[
              ...product?.images,
              ...product?.productGroup?.otherImages,
              ...product?.productGroup?.thumbnail,
            ].map((image, index) => (
              <div key={index} className="size-50 max-md:size-40">
                <img
                  src={image}
                  alt={index}
                  className="size-full object-contain object-center"
                />
              </div>
            ))}
          </Carousel>
        ) : (
          <div className="size-50 max-md:size-40">
            <img
              src={product?.images[0]}
              alt={product?.productGroup?.name}
              className="size-full object-contain object-center"
            />
          </div>
        )}
        <div className="absolute bottom-2 left-2 z-[5]">
          {averageRating > 0 && (
            <RatingBadge
              rating={averageRating}
              totalRatings={totalReviews}
              ratingsBreakdown={ratingsBreakdown}
              dropdown={dropdown}
            />
          )}
        </div>
        <button
          type="button"
          onClick={toggleWishlist}
          className="absolute top-2 right-2 z-[5] p-1 bg-white bg-opacity-50 rounded-full flex justify-center items-center"
        >
          {isWished(product._id) ? (
            <FaHeart className="text-primeRed" size={18} />
          ) : (
            <FaRegHeart className="text-primeRed" size={18} />
          )}
        </button>
        {product?.isUtsav && (
          <div className="absolute top-2 left-2 z-[5]">
            <img src={utsavBadge} alt="utsav" className="h-2" />
          </div>
        )}
      </div>

      <div className="ITEM-DETAILS p-2 leading-none font-secondary flex-grow">
        <p className="NAME text-sm max-md:text-xs font-medium line-clamp-1 text-center">
          {product?.name}
        </p>
        <Link
          to={`/brand/${product?.productGroup?.brand?._id}`}
          className="BRAND font-medium text-xs max-lg:text-xxs text-primary text-center leading-none"
        >
          <p>{product?.productGroup?.brand?.name}</p>
        </Link>
      </div>

      <div className="BOTTOM-SECTION flex flex-col justify-center items-center gap-y-1 p-2 pt-0">
        <div className="md:space-y-1">
          <div className="flex items-center gap-2">
            <p className="text-xs max-md:text-xxs text-center font-medium text-primary line-through leading-none">
              ₹ {Math.round(product?.unitPrice)}
            </p>
            <p className="font-bold text-lg max-md:text-sm md:text-center leading-none">
              ₹ {Math.round(product?.sellingPrice)}
            </p>
            {product?.discount > 0 && (
              <span className="leading-none text-sm max-md:text-xs font-medium text-green-500">
                {product?.discountType === "percent"
                  ? `${product.discount}%`
                  : `₹${product.discount} OFF`}
              </span>
            )}
          </div>
        </div>
        <div className="UTSAV-PRICING flex items-center lg:gap-2 gap-1 whitespace-nowrap text-success">
          <p className="font-medium leading-none max-lg:text-xs">Utsav Price</p>
          <p className="text-lg max-lg:text-sm font-bold leading-none">
            ₹ {Math.round(product.utsavPrice)}
          </p>
        </div>
      </div>
    </div>
  );
};

export default ProductCard;
