import { keepPreviousData, useQuery } from "@tanstack/react-query";
import {
  getAllTopSellingProducts,
  getAllVariants,
  getFeaturedVariantsBasedOnCategory,
  getRecommendation,
  getVariant,
} from "../api/productApis";

const useVariants = (params) => {
  return useQuery({
    queryKey: ["variants", params],
    queryFn: () => getAllVariants(params),
    staleTime: 1000 * 60 * 5,
    placeholderData: keepPreviousData,
  });
};

const useVariantById = (variantId) => {
  return useQuery({
    queryKey: ["variant", variantId],
    queryFn: () => getVariant(variantId),
    staleTime: 1000 * 60 * 5,
    enabled: !!variantId,
  });
};

const useRecommendation = (variantId) => {
  return useQuery({
    queryKey: ["recommendations", variantId],
    queryFn: () => getRecommendation(variantId),
    staleTime: 1000 * 60 * 5,
    placeholderData: [],
    enabled: !!variantId,
  });
};

const useFeaturedVariantsBasedOnCategory = (categoryId) => {
  return useQuery({
    queryKey: ["variants", "featured", categoryId],
    queryFn: () => getFeaturedVariantsBasedOnCategory(categoryId),
  });
};

const useTopSellingProducts = () => {
  return useQuery({
    queryKey: ["products", "top selling"],
    queryFn: () => getAllTopSellingProducts(),
  });
};

export {
  useVariants,
  useVariantById,
  useRecommendation,
  useFeaturedVariantsBasedOnCategory,
  useTopSellingProducts,
};
