import Rating from "../../components/Rating";
import Button from "../../components/Button";
import QuantityInput from "../../components/QuantityInput";
import { Link, useNavigate } from "react-router-dom";
import { useCart } from "../../contexts/cartContext";
import useReview from "../../hooks/useReview";
import useProduct from "../../hooks/useProduct";
import Variation from "./Variation";
import useBrand from "../../hooks/useBrand";
import RatingBadge from "../../components/RatingBadge";
import { BiShoppingBag } from "react-icons/bi";
import { FiShoppingBag } from "react-icons/fi";
import { useCheckout } from "../../contexts/checkoutContext";
import { useModal } from "../../contexts/modalContext";
import UtsavModal from "../../components/UtsavModal";

const Details = ({ product, handlePreview }) => {
  const { addToCart, removeFromCart, itemInCart } = useCart();
  const { averageRating } = useReview(product.productGroup._id);
  const { brand } = useBrand(product?.productGroup?.brand);
  const { setBillingItems } = useCheckout();
  const { product: productGroup } = useProduct(product?.productGroup?._id);
  const navigate = useNavigate();
  const { openModal } = useModal();

  const toggleCart = () => {
    if (itemInCart(product._id)) {
      removeFromCart(product);
    } else {
      addToCart(product);
    }
  };

  const buyNow = (product) => {
    if (!itemInCart(product._id)) {
      addToCart(product, 1);
    }
    setBillingItems([{ productId: product, itemQuantity: 1 }]);
    navigate("/checkout");
  };

  return (
    productGroup && (
      <section>
        {brand && (
          <Link
            to={`/brand/${brand._id}`}
            className="BRAND-NAME capitalize font-medium text-primary max-sm:text-sm"
          >
            {brand.name}
          </Link>
        )}

        <h1 className="PRODUCT-NAME text-xl max-sm:text-lg font-secondary font-medium mb-1">
          {product.name}
        </h1>

        {averageRating > 0 && (
          <div className="RATING-BOX flex items-center gap-2">
            <Rating rating={averageRating} size={20} />
            <RatingBadge rating={averageRating} />
          </div>
        )}

        <div className="PRICE-TAG mt-4 flex gap-2 items-center font-medium text-lg">
          <p className="line-through text-primary">₹ {product?.unitPrice}</p>
          <p className="font-bold text-2xl">₹ {product?.sellingPrice}</p>
          {product?.discount > 0 && (
            <p className="text-hotPink font-bold">
              {product?.discountType === "percent"
                ? `${product.discount}% OFF`
                : `FLAT ₹${product.discount} OFF`}
            </p>
          )}
        </div>

        <div className="UTSAV-PRICE-TAG mb-4 flex gap-2 items-center font-medium text-lg text-primeRed">
          <p>UTSAV PRICE</p>
          <p className="font-bold text-2xl">₹ {product?.utsavPrice}</p>
        </div>

        <div className="my-4 py-4">
          <div className="bg-gradient-to-r from-yellow-300 via-red-500 to-purple-600 text-white p-2 rounded-lg shadow-md text-center space-y-2">
            <h2 className="text-xl max-md:text-base font-extrabold tracking-wide">
              🎉 Utsav Members Get an{" "}
              <span className="text-3xl max-md:text-2xl">Extra Discount</span>{" "}
              Up to <span className="text-3xl max-md:text-2xl">25%</span>! 🎉
            </h2>
            <p
              onClick={() => openModal(<UtsavModal />)}
              className="text-xs text-white text-right leading-none cursor-pointer"
            >
              Know more
            </p>
          </div>
        </div>

        <Variation
          product={productGroup}
          variant={product}
          onPreview={handlePreview}
        />

        <div className="BTN-SECTION my-8 flex gap-8 items-center">
          {itemInCart(product._id) && (
            <QuantityInput
              readOnly
              value={itemInCart(product._id).itemQuantity}
              onIncrease={() => addToCart(product, 1)}
              onDecrease={() => addToCart(product, -1)}
              containerClassName="border-primeRed h-9 rounded-md"
              btnClass="border-primeRed px-1"
            />
          )}
          {!itemInCart(product._id) && (
            <Button onClick={toggleCart}>
              <div className="flex items-center gap-2">
                <p className="text-sm">Add to Bag</p>
                <span>
                  <BiShoppingBag />
                </span>
              </div>
            </Button>
          )}
          <Button onClick={() => buyNow(product)} reverse>
            <div className="flex items-center gap-4">
              <span>
                <FiShoppingBag />
              </span>
              buy now
            </div>
          </Button>
        </div>
      </section>
    )
  );
};

export default Details;
